import Grid from "@mui/material/Grid";
import Paper from "@mui/material/Paper";
import Stack from "@mui/material/Stack";
import Container from "@mui/material/Container";
import React from "react";
import FormatListBulletedIcon from "@mui/icons-material/FormatListBulleted";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import makeStyles from "@mui/styles/makeStyles";
import {
  Banner,
  SearchBar,
  CardImg,
  ProfileBar,
  BottomNav,
} from "../../components";
import { useNavigate } from "react-router-dom";
import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, { Pagination } from "swiper";

const useStyles = makeStyles(() => ({
  textContent: {
    padding: "0 8px !important",
    flexGrow: 100,
  },
  filterContent: {
    flexGrow: 100,
  },
  searchBar: {
    display: "flex",
  },
  root: {
    paddingTop: "16px",
  },
}));

const HomePage = () => {
  const classes = useStyles();
  const navigate = useNavigate();
  return (
    <Container className={classes.root}>
      <Stack spacing={2}>
        <ProfileBar />
        <Typography variant="subtitle2">Promosi</Typography>
        <Swiper
          // pagination={true}
          slidesPerView={2.1}
          spaceBetween={10}
          className="mySwiper"
        >
          {[1, 2, 3, 4].map((item, index) => {
            return (
              <SwiperSlide key={"carousel_" + index}>
                <Banner />
              </SwiperSlide>
            );
          })}
        </Swiper>
        <Grid container>
          <Grid container item xs={12} spacing={2}>
            {[
              {
                title: "Pelayanan",
                click: () => navigate("/make-appointment"),
                disabled: true,
              },
              {
                title: "Dokter",
              click: () => navigate("/doctor-home"),
                disabled: false,
              },
              {
                title: "Info Kamar",
                click: () => navigate("/rooms"),
                disabled: false,
              },
              {
                title: "MCU",
                click: () => navigate("/make-appointment"),
                disabled: true,
              },
              {
                title: "Quick Test",
                click: () => navigate("/make-appointment"),
                disabled: true,
              },
              {
                title: "Vaksin",
                click: () => navigate("/register-vaksin"),
                disabled: true,
              },
              {
                title: "Kehamilan",
                click: () => navigate("/make-appointment"),
                disabled: true,
              },
              {
                title: "Hemodialisa",
                click: () => navigate("/make-appointment"),
                disabled: true,
              },
            ].map((item) => {
              return (
                <Grid item xs={3}>
                  <CardImg
                    type="small"
                    title={item.title}
                    text="Bedah Hati"
                    click={item.click}
                    disabled={item.disabled}
                  />
                </Grid>
              );
            })}
          </Grid>
        </Grid>
        <Typography variant="subtitle2">Berita</Typography>
        <Swiper
          // pagination={true}
          slidesPerView={2.1}
          spaceBetween={10}
          className="mySwiper"
        >
          {[1, 2, 3, 4].map((item, index) => {
            return (
              <SwiperSlide key={"carousel_" + index}>
                <Banner />
              </SwiperSlide>
            );
          })}
        </Swiper>
      </Stack>
      <BottomNav />
    </Container>
  );
};

export default HomePage;
